.Call {
    color: var(--text-color);
}

@property --a {
    syntax: '<angle>';
    inherits: false;
    initial-value: 45deg;
}

@property --ba {
    syntax: '<color>';
    inherits: false;
    initial-value: #20A4D7;
}

@property --bb {
    syntax: '<color>';
    inherits: false;
    initial-value: #3F8BEA;
}

@property --bc {
    syntax: '<color>';
    inherits: false;
    initial-value: #8148EC;
}

@property --bd {
    syntax: '<color>';
    inherits: false;
    initial-value: #B456D8;
}

@property --bf {
    syntax: '<percentage>';
    inherits: false;
    initial-value: 65%;
}

.background.prev {
    z-index: -2;
}

.Column {
    flex: 1;
    max-width: 420px;
    padding-left: 16px;
    margin-left: -16px;
    border-right: 1px solid #ffffff55;
    border-radius: 0 16px 16px 0;
    position: relative;
    overflow: hidden;
    background-color: var(--background);
    z-index: 2;
    transition: all .3s ease;
    /*  allow-discrete */
}

.Column.hidden,
.Column.animate {
    margin-left: -420px !important;
    max-width: 420px !important;
    /* display: none; */
}

.Column.showAnim {
    transition: none;
}

.Column.Maximized {
    flex: 2;
    max-width: 100%;

}

.Call {
    width: 100%;
    height: 100%;
    position: relative;
}

.Call .TopBar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 54px;
    user-select: none;
}

.Call .background {
    background-image: var(--blue-violet);
    position: absolute;
    z-index: -1;
    transform: rotate(0deg) translateY(-50%);
    /* transform-origin: top; */
    top: 50%;
    animation: backgroundRotate 15s linear infinite;
    margin-left: -16px;
    width: calc(100% + 16px);
    transition: all 1s ease-in-out;
}

.Call .background.connected {
    animation: backgroundRotate 10s linear infinite, connectedBackground 10s linear;
}

@supports (background: paint(something)) {

    .Call .background {
        background-image: linear-gradient(var(--a), var(--ba) 0%, var(--bb), var(--bc) 100%);
    }
}

.mask {
    overflow: hidden;
    width: 7.25rem;
    height: 7.25rem;
    min-width: 7.25rem;
    min-height: 7.25rem;
    position: fixed;
    margin: auto;
    left: 0;
    right: 0;
    top: 120px;
    border-radius: 50%;
    z-index: -1;
    transition: all .5s ease;
}

.mask.animate {
    width: 100%;
    height: 100%;
    transform: scale(1.8);
    top: -120px;
}

.mask .background {
    position: absolute;
}

@keyframes backgroundRotate {
    from {
        /* transform: rotate(0deg); */
        --a: 0deg;
    }

    20% {
        --bf: 0%;
    }

    30% {
        --bf: 0%
    }

    50% {
        --bf: 65%;
    }

    60% {
        --bf: 65%;
    }

    80% {
        --bf: 0%
    }

    to {
        /* transform: rotate(360deg); */
        --a: 360deg;
        --bf: 65%
    }
}

@keyframes connectedBackground {
    10% {
        --ba: #a9cc66;
        --bb: #5ab147;
        --bc: #07ba63;
        --bd: #07a9ac;
    }

    20% {
        --ba: #a9cc66;
        --bb: #5ab147;
        --bc: #07ba63;
        --bd: #07a9ac;
    }

    60% {
        --ba: #08b0a3;
        --bb: #17aae4;
        --bc: #3b7af1;
        --bd: #4576e9;
    }

    100% {
        --ba: #20a4d7;
        --bb: #3f8bea;
        --bc: #8148ec;
        --bd: #b456d8;
    }
}

.Call .CallOutput {
    display: flex;
    justify-content: space-around;
    margin: 0;
    height: calc(100% - 54px);
    position: relative;
}

.Call .User .UserInfo {
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    top: 120px;
    top: 15%;
    position: relative;
    /* z-index: -1; */
    transition: all .5s ease;
}

.Call .User .UserInfo .SoundBubbles {
    width: 7.25rem;
    height: 7.25rem;
    position: absolute;
    transition: all .3s ease;
}

.Call .User .UserInfo .SoundBubbles div {
    width: 7.25rem;
    height: 7.25rem;
    position: absolute;
    border-radius: 44%;
}

.Call .User .UserInfo .SoundBubbles .first {
    background-color: rgb(255 255 255 / 16%);
    scale: 1.05;
    rotate: 40deg;
    /* transform: scale(1.1) rotate(45deg); */
    animation: bubbles 5s infinite reverse;
}

.Call .User .UserInfo .SoundBubbles .second {
    background-color: rgb(255 255 255 / 20%);
    scale: 1.15;
    rotate: 15deg;
    /* transform: scale(1.2) rotate(45deg); */
    animation: bubbles 5s infinite;
}

@keyframes bubbles {
    0% {
        border-radius: 43%;
        transform: scale(1.0) rotate(45deg);
    }

    50% {
        border-radius: 47%;
        transform: scale(1.05) rotate(40deg);
    }

    100% {
        border-radius: 43%;
        transform: scale(1.0) rotate(45deg);
    }
}

.Call .User .UserInfo .Name {
    padding-top: 20px;
    font-size: 24px;
}

.Call .User .UserInfo .status {
    font-size: 16px;
    padding-top: 4px;
    display: flex;
    align-items: center;
    gap: 0 4px;
    transition: all .2s ease-in-out;
}

.Call .User .UserInfo .status.animate {
    transform: translateY(5px);
    opacity: 0;
}

.Call .User .UserInfo .status .icon {
    margin: 0;
    padding: 0;
}

.Call .profile {
    width: 7.25rem;
    height: 7.25rem;
    background: linear-gradient(45deg, #1788d1, #31bdff);
    border-radius: 50%;
    box-shadow: 0 4px 30px rgb(0 0 0 / 10%);
    backdrop-filter: blur(6.9px);
    -webkit-backdrop-filter: blur(6.9px);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 48px;
    position: relative;
    transition: all .3s ease-in-out;
}

.Call .Dark .profile {
    color: #000;
}

.Call video {
    width: 100%;
    height: 100%;
    display: block;
}

#ourVideo {
    width: 32vw;
    min-height: 10vh;
    max-width: 180px;
    max-height: 30vh;
    position: absolute;
    right: 7px;
    bottom: 125px;
    background-color: #00000075;
    border-radius: 12px;
    overflow: hidden;
    z-index: 5;
    transition: all .3s ease-in-out;
}

#ourVideo .video {
    border-radius: 12px;
}

#ourVideo.animate {
    opacity: 0;
    transform: scale(.95);
}

#remoteVideo {
    width: 100%;
    height: 100%;
    margin: auto;
    position: relative;
}

#remoteVideo video {
    position: absolute;
    top: calc(50% - 54px);
    left: 0;
    transform: translateY(-50%);
    width: 100%;
    height: auto;
    border-radius: 16px;
    transition: opacity .3s ease;
}

video.disabled {
    opacity: 0;
}

.Call.RateCall #ourVideo,
.Call.RateCall video {
    opacity: 0;
}

#remoteVideo .video {
    position: absolute;
    top: 0;
    opacity: 0;
}

.Call .bottom {
    position: absolute;
    bottom: 0px;
    width: 100%;
    left: 0;
    padding: 24px 0;
    margin-left: -16px;
    padding-left: 16px;
    width: calc(100% + 16px);
    text-align: center;
    background: linear-gradient(0deg, #00000040, transparent);
    transition: all .5s ease;
}

.call {
    display: flex;
    justify-content: center;
}

#alert {
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 1;
    transform: translate(-50%, -50%);
    text-transform: capitalize;
    font-size: 28px;
    transition: all .5s ease;
}

#alert span {
    color: #00000075;
    text-shadow: 0 0 60px black;
    font-size: 28px;
}

#peerID {
    height: 50px;
    width: 200px;
    border-radius: 7px;
    border: 1px solid #00000070;
    color: #888;
    padding: 0 10px;
    outline: none;
    background-color: #363640;
}

#call-peer {
    background-color: #3C3C46;
    color: #fff;
    font-size: 18px;
    border-radius: 7px;
    border: none;
    margin-left: -16px;
    height: 52px;
    padding: 0 10px;
    cursor: pointer;
}

.Call .call-buttons,
.Call .incoming-buttons {
    display: flex;
    justify-content: space-evenly;
    transition: all .3s ease;
}

.Call .bottom button {
    border-radius: 50px;
    outline: none;
    border: none;
    width: 60px;
    height: 60px;
    margin: 0 4px;
    background-color: #ffffff30;
    backdrop-filter: contrast(1.5);
    cursor: pointer;
    transition: all .3s ease;
}

.Call .bottom button.disabled {
    background-color: #ffffff30 !important;
    opacity: .5;
}

.Call .bottom .call-buttons button.disabled .icon {
    color: #fff !important;
}

.Call .bottom button:hover {
    background-color: #00000090;
    transition: all .3s ease;
}

.Call .bottom .call-buttons span,
.Call .bottom .incoming-buttons span {
    color: #fff;
    display: block;
    font-size: 14px;
    padding-top: 7px;
}

.Call .bottom .icon {
    font-size: 32px !important;
    padding: 0;
    color: #fff;
    background: none;
}

.Call .bottom .alt button {
    background-color: #fff;
}

.Call .bottom .call-buttons .alt button .icon {
    color: #000;
}

.Call .hidden {
    display: none !important;
}

#call-disconnect {
    background-color: #ff0000;
}

#call-disconnect:hover {
    background-color: #c00000;
}

#callAnswer {
    background-color: #00e000;
}

#callAnswer:hover {
    background-color: #00c000;
}

#callDecline {
    background-color: #ff0000;
}

#callDecline:hover {
    background-color: #c00000;
}

.RateCall .call-buttons {
    opacity: 0;
}

.RateCall .CallOutput #remoteVideo .UserInfo {
    margin-top: -24px;
}

.RateCall .CallOutput #remoteVideo .UserInfo .profile {
    animation: shrink_grow .5s 1;
}

.RateCall .CallOutput #remoteVideo .UserInfo .SoundBubbles {
    transform: scale(.8);
}

.RateCall .call-buttons button {
    transform: scale(.8);
    transition: all .3s ease;
}

@keyframes shrink_grow {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(.9);
    }

    100% {
        transform: scale(1);
    }
}

.Call .bottom button.close {
    /* width: calc(100% - 108px - 24px); */
    position: absolute;
    margin: 0 24px;
    border-radius: 12px;
    min-width: 60px;
    height: 54px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background-color: #ffffffcc;
    backdrop-filter: contrast(2.5);
    bottom: 48px;
    transition: all .7s ease;
    z-index: 1;
}

.Call .bottom button.close:hover {
    background-color: #ffffffdd;
    transition: background-color .3s ease;
}

.Call .bottom .close.animate {
    position: absolute;
    width: 60px;
    height: 60px;
    background-color: #ff0000;
    border-radius: 50px;
}

.Call .bottom .close span {
    color: #3F8BEA;
    font-weight: 500;
    font-family: Roboto;
    font-size: 18px;
    padding: 0;
}

.rate {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 16px 24px;
    margin-top: 32px;
    font-size: 14px;
    line-height: 1.5;
    border-radius: 20px;
    background-color: #0002;
    backdrop-filter: contrast(1.5);
    position: relative;
    transition: all .5s ease;
}

.rate.animate {
    transform: scale(.9);
    opacity: 0;
}

.rate .title {
    font-weight: 500;
}

.rate .subtitle {
    line-height: 2;
}

.rate .stars {
    padding-top: 8px;
}

.rate .stars .icon {
    font-size: 36px !important;
    margin: 0;
    transform: scale(.8) translateY(16px);
    opacity: 0;
    cursor: pointer;
    background-color: transparent !important;
}

.rate .stars .icon.active {
    opacity: 1;
    transform: none;
}

@keyframes starAnimation {
    from {
        transform: scale(.8) translateY(16px);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes starActive {
    from {
        transform: scale(.9);
    }

    to {
        color: #fff;
        transform: scale(1);
        font-variation-settings:
            'FILL' 1;
    }
}

.rate .stars.animate .icon:nth-child(1) {
    animation: starAnimation .3s ease .3s forwards;
}

.rate .stars.animate .icon:nth-child(2) {
    animation: starAnimation .3s ease .35s forwards;
}

.rate .stars.animate .icon:nth-child(3) {
    animation: starAnimation .3s ease .4s forwards;
}

.rate .stars.animate .icon:nth-child(4) {
    animation: starAnimation .3s ease .45s forwards;
}

.rate .stars.animate .icon:nth-child(5) {
    animation: starAnimation .3s ease .5s forwards;
}

/* --------------------- */
.rate .stars.animate .icon.active {
    transform: scale(.9);
}

.rate .stars.animate .icon.active:nth-child(1) {
    animation: starAnimation .3s ease .5s forwards,
        starActive .3s ease forwards;
}

.rate .stars.animate .icon.active:nth-child(2) {
    animation: starAnimation .3s ease .5s forwards,
        starActive .3s ease .05s forwards;
}

.rate .stars.animate .icon.active:nth-child(3) {
    animation: starAnimation .3s ease .5s forwards,
        starActive .3s ease .1s forwards;
}

.rate .stars.animate .icon.active:nth-child(4) {
    animation: starAnimation .3s ease .5s forwards,
        starActive .3s ease .15s forwards;
}

.rate .stars.animate .icon.active:nth-child(5) {
    animation: starAnimation .3s ease .5s forwards,
        starActive .3s ease .2s forwards;
}

.invisible {
    display: none;
}

.default {
    background: var(--default);
}

.blue-violet {
    background: var(--blue-violet);
}

.purple {
    background: var(--purple);
}

.blue-green {
    background: var(--blue-green);
}

.green {
    background: var(--green);
}

.black {
    background: var(--black);
}

.Call .Dark {
    --primary: #006095;
    --primary-container: #1e1e1e;
    --secondary: #121212;
    --on-primary: #272727;
    --active-primary: #373737;
    --text-color: #d5d5d5;
    /* ------------ Color Themes ------------ */
    --default: linear-gradient(120deg, #3b7493, #00345e);
    --blue-purple: linear-gradient(120deg, #00345e 30%, #713b93 70%);
    --purple: linear-gradient(120deg, #713b93, #28005e);
    --blue-green: linear-gradient(120deg, #00345e 40%, #005e5a 60%);
    --green: linear-gradient(120deg, #65933b, #005e5a);
    --black: linear-gradient(120deg, #202020, #080808);
    color: var(--text-color);
}

.Call.Minimal {
    height: 54px;
    bottom: 32px;
    border-radius: 16px;
    position: absolute;
    width: calc(100% - 80px);
    margin: 0 40px;
    overflow: hidden;
    z-index: 2;
    transition: all .3s cubic-bezier(.68, -0.55, .27, 1.55);
}

.Call.Minimal.animate {
    opacity: 0;
    transform: scale(.8);
}

.Minimal .CallOutput {
    height: 100%;
    flex: 1;
    cursor: pointer;
}

.Minimal .background {
    background-size: 100% 200%;
}

.Minimal .CallOutput::after {
    content: '';
    width: 104%;
    height: calc(100% - 8px);
    transform: scale(.95);
    position: absolute;
    left: -12px;
    margin: 4px;
    border-radius: 12px;
    z-index: -1;
    transition: all .3s ease;
}

.Minimal .CallOutput:hover::after,
.Minimal .CallOutput.hovered::after {
    background-color: #0004;
    transform: scale(1);
}

.Minimal .bottom {
    display: flex;
    padding: 0;
    width: 100%;
    height: 100%;
    margin: 0;
    align-items: center;
    justify-content: space-between;
    padding: 0 12px;
}

.Minimal .User {
    display: flex;
}

.Minimal .User .UserInfo {
    top: auto;
    flex-direction: row;
    gap: 8px;
}

.Minimal .User .UserInfo .SoundBubbles,
.Minimal .User .UserInfo .SoundBubbles div {
    width: 2.25rem;
    height: 2.25rem;
}

.Minimal .UserInfo .profile {
    width: 2.25rem;
    height: 2.25rem;
    font-size: 18px;
}

.Minimal .User .UserInfo .Details {
    text-align: left;
    line-height: 1;
}

.Minimal .User .UserInfo .Name {
    font-size: 16px;
    padding-top: 0;
}

.Minimal .User .UserInfo .status {
    font-size: 12px;
}

.Minimal .bottom .call-buttons span,
.Minimal .bottom .incoming-buttons span {
    display: none;
}

.Minimal .bottom button {
    width: 32px;
    height: 32px;
}

.Minimal .bottom .icon {
    font-size: 20px !important;
    margin: 0;
    background-color: transparent !important;
}

.maximize {
    position: absolute;
    right: 0;
    top: 54px;
    width: 20px;
    height: calc(100% - 54px);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 16px;
    padding-bottom: 54px;
    transform: scaleX(.8) scaleY(.95);
    cursor: pointer;
    transition: all .3s ease;
}

.maximize:hover {
    background-color: #0003;
    transform: scale(1);
}

.maximize .icon {
    background: none;
}

.particles::after {
    position: absolute;
    content: "";
    left: -105px;
    top: -105px;
    min-width: 300px;
    min-height: 300px;
    pointer-events: none;
    transition: background-size ease-in-out 0.5s,
        background-position ease-in-out 0.5s;
    background-repeat: no-repeat;
    overflow: hidden;
}

.stars .icon.animated::after {
    animation: theButtonAnimation linear 2s forwards;
    background-image:
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%),
        radial-gradient(circle, rgba(255, 255, 255, 1) 45%, transparent 45%);
}

