@media screen and (max-width: 480px) {
    .Auth.container {
        max-width: none !important;
        height: 100%;
    }

    .Auth.container .content {
        flex: 1;
    }

    .Home {
        margin: 0;
        height: 100%;
        border-radius: 0;
    }

    .LeftColumn {
        width: 100% !important;
        min-width: 0 !important;
        max-width: 26.5rem !important;
        border-radius: 0;
        z-index: 0;
    }

    /* .MiddleColumn {
        display: none;
    }

    .MiddleColumn.active {
        display: flex;
    } */

    .MiddleColumn {
        position: fixed;
        width: 100%;
        height: 100%;
    }

    .MiddleColumn .background {
        display: block !important;
    }

    /* .MiddleColumn .background::after {
        content: '';
        position: fixed;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: #ffffff10;
    } */

    .MiddleColumn .ChatInfo .BackArrow {
        display: block;
    }

    .Column {
        border-radius: 0 !important;
    }

    .Home .ContextMenuBG {
        opacity: unset;
    }

    .Home .MenuItem {
        padding: 2px 0;
        min-height: 38px;
    }

    .Message.Out .bubble {
        max-width: min(30rem, 100vw - 3.75rem);
    }

    .Message.In .bubble {
        max-width: min(29rem, 100vw - 7rem);
    }

    /* .message-media {
        width: calc(60vw + .575rem * 2) !important;
    } */
}